.date-scroller-container {
  margin-bottom: 20px;
}

.month-year {
  font-size: 0.9rem;
  font-weight: bold;
  color: #3498db;
  margin-bottom: 5px;
  cursor: pointer;
  display: block; /* Ensure it's visible */
}

.date-scroller {
  display: flex;
  align-items: center;
}

.dates-wrapper {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.dates-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow-x: hidden; /* Hide horizontal overflow */
  padding: 0 10px;
  width: 100%;
}

.dates-container::-webkit-scrollbar {
  height: 4px; /* Smaller height for the scrollbar */
}

.dates-container::-webkit-scrollbar-thumb {
  background-color: #3498db;
  border-radius: 10px;
}

.dates-container::-webkit-scrollbar-track {
  background-color: transparent;
}

.date-item {
  flex: 1;
  text-align: center;
  min-width: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-radius: 50%;
  cursor: pointer;
  transition: transform 0.3s ease, font-size 0.3s ease;
  box-sizing: border-box;
  position: relative;
}

.date-item::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: transparent;
  transform: translate(-50%, -50%);
  transition: background-color 0.3s ease;
}

.date-item:hover::before {
  background-color: rgba(52, 152, 219, 0.2); /* Light blue circle on hover */
}

.date-item.selected {
  transform: scale(.9); /* Make the selected date larger */
  font-weight: bold; /* Bold the text */
}

.date-item.selected::before {
  border: 2px solid #3498db; /* Add a blue border around the selected date */
  width: 55px; /* Adjust width for the border */
  height: 55px; /* Adjust height for the border */
}

.weekday {
  font-size: 0.8em;
  color: #7f8c8d;
}

.day {
  font-size: 1.2em;
  font-weight: bold;
  color: #2c3e50;
}

.scroll-button {
  background: none;
  border: none;
  font-size: 1.5em;
  cursor: pointer;
  padding: 0 10px;
  color: #3498db;
  transition: color 0.3s ease;
}

.scroll-button:hover {
  color: #2980b9;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .date-scroller {
    padding: 5px 0;
  }

  .date-item {
    padding: 8px;
    min-width: 40px;
  }

  .weekday {
    font-size: 0.7em;
  }

  .day {
    font-size: 1em;
  }
}
